    font-family: 'Germania One', cursive;
    font-size: 50px;
    color: black;
  text-align: center;
}

h2{
    font-family: 'Germania One', cursive;
    font-size: 25px; 
    color: black;
}

img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
}


.card {
  height: 200px;
  width: 150px;
  border: black solid 5px;
  background: white;
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
}

img {
  height: 180px;
  width: 125px;
  -webkit-align-content: center;
          align-content: center;
}


.wrapper {
  padding-top: 50px;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

body {
  background: white;
}
h2 {
  width: 100%;
  text-align: center;
  font-family: 'Germania One', cursive;
  font-size: 25px;
  color:black;
}

.grid-container {
  display: grid;
  grid-gap: 16px;  
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

